html {
  background-color: #d3d0d7;
  height: 100%; }

body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; }

.keys,
.sliders,
.controls,
.display,
.drum-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
